<template>
<div class="scrollBox">
  <!-- <header-nav :msg="$utils.splitStr(details.vod_name, 10)"></header-nav> -->
  <div class="backIcon" @click="back"></div>
  <div id='wechatDetail' class="iconSize">
    <div class="iconSize boxBg">
      <img src="../../../assets/img/movie_detail/bg.png" alt="">
      <div class="iconSize movie pos">
        <img src="../../../assets/img/movie_detail/movie.png" alt="电影">
      </div>
      <div class="iconSize tv pos">
        <img src="../../../assets/img/movie_detail/tv.png" alt="电视剧">
      </div>
      <div class="iconSize cartoon pos">
        <img src="../../../assets/img/movie_detail/cartoon.png" alt="动漫">
      </div>
      <div class="iconSize variety pos">
        <img src="../../../assets/img/movie_detail/variety.png" alt="综艺">
      </div>
      <div class="iconSize phone pos">
        <img src="../../../assets/img/movie_detail/phone.png" alt="手机">
        <div class="cover">
          <img :src="details.vod_pic" onerror="this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/default_image.png'" alt="">
        </div>
      </div>
      <div class="iconSize bottom-bg pos">
        <img src="../../../assets/img/movie_detail/bottom_bg.png" alt="底部背景">
        <div class="movie-info" v-if="details">
          <div class="title">《{{$utils.splitStr(details.vod_name, 10)}}》</div>
          <div class="vod_remark vod_content">{{details.type_id_1 === 1 ? `${details.vod_year > 1900 ? details.vod_year + '年上映' : '暂无上映时间'}` : $utils.splitStr(details.vod_remarks, 10)}}</div>
          <div class="filmClass vod_content">
            <span v-if="details.vod_area">{{$utils.splitStr(details.vod_area, 5)}}</span>
            <span v-if="details.vod_class">{{details.vod_class ? details.vod_class.split('/')[0] : '暂无分类'}}</span>
            <span v-if="details.vod_hits">{{details.vod_hits | setFloatNumber}}次播放</span>
          </div>
          <div class="leader vod_content">导演 {{details.vod_director ? $utils.splitStr(details.vod_director, 13) : ''}}</div>
          <div class="leader vod_content">主演 {{details.vod_actor ? $utils.splitStr(details.vod_actor, 17) : ''}}</div>
          <div class="tip">因微信、QQ、部分浏览器屏蔽播放线路，请下载APP观看</div>
          <div class="button" @click="todownLoad">前往下载</div>
        </div>
        <div class="bottom-info">
          <div class="text">“掘金宝” 拥有全球最大影视片库，</div>
          <div class="text">覆盖1930~2019年优秀影片，最新最热一网打尽</div>
        </div>
      </div>
    </div>
  </div>
</div>
  
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
export default {
  components: {
    HeaderNav
  },
  data () {
    return {
      details: {}
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getDetail () {
      if (!this.$route.query.id) {
        return
      }
      this.$api.get('MOVIE_DETAIL', {vod_id: this.$route.query.id, is_browser: 1}, res => {
        this.details = res.data
      })
    },
    todownLoad () {
      this.$utils.openDownUrl()
    }
  },
  mounted () {
    this.getDetail()
  }
}
</script>
<style lang="scss" scoped>
#wechatDetail {
  height: 100vh;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  .boxBg {
    position: relative;
  }
  > img {
    display: block;
    width: 100%;
  }
  .pos {
    position: absolute;
  }
  .phone {
    width: 465px;
    height: 934px;
    top: 300px;
    left: 140px;
    .cover {
      width: 420px;
      height: 586px;
      overflow: hidden;
      top: 98px;
      left: 24px;
      position: absolute;
      img {
        width: 100%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
      }
    }
  }
  .button {
    width: 482px;
    height: 124px;
    background: url('../../../assets/img/movie_detail/button.png') no-repeat left top;
    background-size: 100%;
    color: #fff;
    text-align: center;
    line-height: 124px;
    top: 1385px;
    z-index: 4;
    font-size: 46px;
    margin: 0 auto;
  }
  .movie {
    top: 280px;
    left: 0;
    width: 135px;
    height: 135px;
    z-index: 1;

  }
  .tv {
    top: 374px;
    right: 30px;
    width: 168px;
    height: 168px;
    z-index: 3;
  }
  .cartoon {
    top: 725px;
    left: 28px;
    width: 174px;
    height: 174px;
    z-index: 3;
  }
  .variety {
    width: 125px;
    height: 125px;
    top: 825px;
    right: 100px;
    z-index: 4;
  }
  .bottom-bg {
    top: 892px;
    width: 100%;
    height: 838px;
    .movie-info {
      width: 100%;
      height: 420px;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      padding-top: 90px;
      box-sizing: border-box;
      text-align: center;
      .title {
        line-height: 50px;
        margin-bottom: 20px;
        font-size: 40px;
        font-weight: 900;
      }
      .tip {
        margin-top: 74px;
        margin-bottom: 24px;
        white-space: nowrap;
        font-size: 28px;
        color: rgb(255, 65, 94)
      }
      .vod_content {
        font-size: 32px;
        line-height: 1.1;
        margin-bottom: 15px;
      }
      .vod_remark {
        margin-bottom: 45px;
      }
    }
    .bottom-info {
      position: absolute;
      bottom: 0;
      width: 750px;
      height: 170px;
      padding-top: 40px;
      box-sizing: border-box;
      font-size: 28px;
      line-height: 40px;
      text-align: center;
      color: rgb(153, 153, 153);
      .text:last-child {
        margin-top: 14px;
      }
    }
  }
  .iconSize {
    img {
      display: block;
    }
  }
}
.backIcon {
  position: fixed;
  left: 0;
  z-index: 2;
  top: 0;
  width: 80px;
  height: 88px;
  background: url('../../../assets/img/details/back.png') no-repeat center;
  background-size: 56px 56px;
}
</style>



